import React from "react";
//style
import { Wrapper, Content, Text } from "./WelcomeSection.style";

const WelcomeSection = ({ image, title, text }) =>(
    <Wrapper image={image}>
        <Content>
            <Text>
                <h1>{title}</h1>
                <p>{text}</p>
            </Text>
        </Content>
    </Wrapper>
)

export default WelcomeSection;